<template>
  <div class="commercePurchaseList">
    <navigation-bar pageName="采购订单" right-text :fixed="true" @onLeftClick="onClickLeft"/>
    <div class="searchPanel">
      <div class="searchInput" @click="gotoSearch()">
        <img class="searchImg" :src="serachIcon" />
        搜索采购单号
      </div>
    </div>
    <div v-if="purchaseList.length <=0" class="noMessagePanel">
      <div class="noMessage">
        <img :src="noList" class="noMessageImg" />
        <p>暂无订单</p>
      </div>
    </div>
    <div class="commerce-over-box">
    <van-list  v-if="purchaseList.length>0" v-model="loading" :finished="finished" finished-text="没有更多了" @load="loadMore" :immediate-check="false">
      <div class="card" v-for="(order,index) in purchaseList" :key="index">
        <!-- 采购单号+时间 -->
        <div class="orderTitlePanel">
          <span class="orderTitleCode">采购单号:{{order.billNo}}</span>
          <span class="orderTime">{{order.createdTime}}</span>
        </div>
        <div class="van-hairline--top"></div>
        <!-- 采购信息 -->
        <div class="orderInfoPanel">
          <img class="orderImg" :src="imgDefault" />
          <div class="orderDetail">
            <div class="orderName">{{order.recAreaCode}}</div>
            <div class="orderName">供应商：{{order.supplierName}}</div>
            <div class="orderName">供应商电话：{{order.supplierPhone}}</div>
          </div>
        </div>
        <div class="dashLine"></div>
        <!-- 商品列表 -->
        <div class="pricePanel" v-for="(sku,index) in order.skuList" :key="index">
          <table style="width: 100%;">
            <tr>
              <td style="width: 80%;">
                <div class="orderGoods">{{sku.goodsName}}</div>
              </td>
              <td style="width: 20%">
                <div class="orderPrice">￥{{sku.skuPrice}}</div>
              </td>
            </tr>
          </table>
          <table style="width: 100%;">
            <tr>
              <td>
                <div class="orderSpecs">{{sku.attrValueContent}}</div>
              </td>
              <td style="width: 40px;">
                <div class="orderGoodsNum">x{{sku.purchaseCount}}</div>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </van-list>
    </div>
  </div>
</template>

<script>
import { getPurchaseListService } from "../../services/commerceService.js";
import { store, storage } from "../../utils/store";
import log from '../../utils/log';
import NavigationBar from "../title/NavigationBar";
export default {
  name: "commercePurchaseList",
  components: { NavigationBar },
  data() {
    return {
      loading: false,
      finished: false,
      purchaseList: [],
      orderData: { page: 1, rows: 10 },
      serachIcon: require("../../assets/imgs/commerce/icon_Shape.png"),
      noList: require("../../assets/imgs/commerce/img_Default@2x.png"),
      imgDefault: require("../../assets/imgs/commerce/back.png"),
      userInfo:null,
      scrollTop: 0
    };
  },
  beforeRouteLeave(to, from, next) {
    this.scrollTop = document.querySelector(".commerce-over-box").scrollTop;
    next();
  },
  activated (){
    //获取用户数据
    this.userInfo = store.get("userInfo");
    this.userInfo = this.userInfo
      ? this.userInfo
      : storage.getStorage("custNews");
    if(this.$route.params.load) {
      // reset data
      this.resetData();
      this.getPurchaseList();
      document.querySelector(".commerce-over-box").scrollTop = 0;
    } else {
      document.querySelector(".commerce-over-box").scrollTop = this.scrollTop;
    }
  },
  methods: {
    resetData() {
      this.loading = false,
      this.finished = false;
      this.purchaseList = [];
      this.orderData = { page: 1, rows: 10 };
      this.orderData.recAreaCode = this.userInfo.AREA_CODE;
    },
    getPurchaseList() {
      let custNews = (store.get("custNews") && store.get("custNews").AREA_CODE) ?
              store.get("custNews") : storage.getStorage("custNews");
      if (this.userInfo.AREA_CODE == "" || this.userInfo.AREA_CODE == undefined) {
        log.i("area code empty, reset first.");
        this.userInfo = (custNews && custNews.AREA_CODE) ? custNews : storage.getStorage("custNews");
        this.orderData.recAreaCode = this.userInfo.AREA_CODE;
      }
      if (this.userInfo.AREA_CODE != custNews.AREA_CODE) {
        log.i("area code changed, reset first.");
        this.userInfo = (custNews && custNews.AREA_CODE) ? custNews : storage.getStorage("custNews");
        this.orderData.recAreaCode = this.userInfo.AREA_CODE;
      }
      this.loading = true;
      getPurchaseListService(this.orderData)
        .then(result => {
          this.loading = false;
          if (result.resultCode == "0") {
            this.purchaseList = this.purchaseList.concat(result.data);
            if (result.total > this.orderData.page * this.orderData.rows) {
              this.finished = false;
              this.orderData.page++;
            } else {
              this.finished = true;
            }
          } else {
            result.msg
              ? this.$toast(result.msg)
              : this.$toast("发送失败，请重试！");
            this.finished = true;
          }
        })
        .catch(error => {
          this.$toast("加载失败，请稍后重试！");
          this.loading = false;
          this.finished = true;
        });
    },
    loadMore() {
      // 异步更新数据
      setTimeout(() => {
        this.getPurchaseList();
      }, 500);
    },
    gotoSearch() {
      this.$router.push({
        path: "/commerceSearch",
        query: {
          orderType: 1
        }
      });
    },
    onClickLeft() {
      this.$router.back();
    }
  }
};
</script>

<style scoped>
.commercePurchaseList {
  background-color: #f2f2f2;
  text-align: left;
  height: 100%;
  box-sizing: border-box;
}

/* 列表搜索 */
.commercePurchaseList .searchPanel {
  height: 44px;
  width: 100%;
  background-color: #00d8d1;
  position: fixed;
  top: 43px;
  z-index: 99;
}

.commercePurchaseList .searchPanel .searchInput {
  height: 33px;
  margin: 0 20px;
  background-color: white;
  color: #9a9a9a;
  font-size: 13px;
  border-radius: 2px;
  text-align: center;
}

.commercePurchaseList .searchPanel .searchInput .searchImg {
  height: 11px;
  width: 11px;
  margin-top: 11px;
}

.commercePurchaseList .commerce-over-box {
  position: absolute;
  top: 88px;
  bottom: 0;
  overflow: auto;
  width: 100%;
}

.commercePurchaseList .card {
  padding-top: 1px;
  padding-bottom: 10px;
  box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.5);
  border-radius: 5px;
  overflow: hidden;
  margin: 10px 10px;
  background-color: #fff;
}

.commercePurchaseList .card .dashLine {
  border: #e5e5e5 0.5px dashed;
  margin: 0 15px;
}

/* 店铺名+订单号 */
.commercePurchaseList .orderTitlePanel {
  height: 40px;
  line-height: 40px;
  padding: 0 15px;
}

.commercePurchaseList .orderTitlePanel .orderTitleCode {
  font-size: 12px;
  color: #4a4a4a;
  letter-spacing: -0.04px;
  font-weight: bold;
  float: left;
}

.commercePurchaseList .orderTitlePanel .orderTime {
  font-size: 12px;
  color: #9a9a9a;
  letter-spacing: -0.04px;
  float: right;
}

/* 商品图片+详情 */
.commercePurchaseList .orderInfoPanel {
  height: 100px;
  display: flex;
}

.commercePurchaseList .orderInfoPanel .orderImg {
  height: 65px;
  width: 65px;
  margin-top: 15px;
  margin-left: 15px;
  border-radius: 2px;
}

.commercePurchaseList .orderInfoPanel .orderDetail {
  margin-top: 18px;
  margin-left: 12px;
}

.commercePurchaseList .orderInfoPanel .orderDetail .orderName {
  height: 20px;
  line-height: 20px;
  font-size: 12px;
}

.commercePurchaseList .orderInfoPanel .orderStatus {
  float: right;
  font-size: 12px;
  color: #ff5050;
  flex-grow: 1;
  margin-top: 18px;
  margin-right: 12px;
  text-align: right;
}

/* 其它商品+总价  */
.commercePurchaseList .pricePanel {
  min-height: 30px;
  margin-top: 10px;
}

.commercePurchaseList .pricePanel .orderGoods {
  font-size: 14px;
  letter-spacing: -0.04px;
  margin-left: 15px;
  /* white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis; */
}

.commercePurchaseList .pricePanel .orderSpecs {
  font-size: 12px;
  color: #9a9a9a;
  margin-left: 15px;
  letter-spacing: -0.04px;
  margin-left: 15px;
}

.commercePurchaseList .pricePanel .orderGoodsNum {
  font-size: 12px;
  color: #9a9a9a;
  letter-spacing: -0.04px;
  margin-left: 8px;
  margin-right: 15px;
  width: 40px;
  text-align: right;
}

.commercePurchaseList .pricePanel .orderPrice {
  margin-right: 15px;
  font-size: 14px;
  color: #333333;
  text-align: right;
}

.commercePurchaseList .lineMargin {
  margin: 10px 15px;
}

.commercePurchaseList .noMessage {
  margin: 1rem auto;
  text-align: center;
  color: #ddd;
  font-size: 13px;
}
.commercePurchaseList .noMessagePanel{
  width: 100%;
  padding-top: 6rem;
}
.commercePurchaseList .noMessageImg{
  display:block;
  width:2rem;
  margin: 0 auto;
}
.commercePurchaseList .van-list,
.commercePurchaseList .van-cell {
  padding: 0;
  background-color: #f2f2f2;
}
</style>
